.InfoWindow {
  position: relative;
  bottom: 110px;
  left: -45px;
  width: 220px;
  /* background: white; */
  background-color: white;
  -webkit-box-shadow: 0 2px 7px 1px rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 7px 1px rgba(0, 0, 0, 0.3);
  padding: 10px;
  font-size: 14px;
  z-index: 100;
}


.InfoWindow:hover .xButton {
  opacity: 1;
}
/* .xButton:hover {
  opacity: 1;
} */

.xButton {
  background: none;
  opacity: .3;
  display: block;
  border: 0px;
  padding: 0px;
  text-transform: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  position: absolute;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  top: -6px;
  right: -6px;
  /* width: '30px';
  height: '30px'; */
  width: 14px;
  height: 14px;
  margin: 8px;
}



